.container{ /* main container to hold all the props*/
  font-family: "courier";
  border:2px solid black;
  display: flex;
  flex-direction: column;
  margin-top: 2%;
  max-height: 450px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
 
}
#cancel{ /*changing cursor icon */
  cursor: pointer; 
}
.Header{ /* alligning all the items inside main container*/
  align-items: center;
  margin: 2%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  
}
.tableWrapper{ /* creating wrapper to wrap the table*/
  margin: 2%;
  border:2px solid black;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-height: 200px;
  overflow: scroll;
  position: relative;
}

#cancel{
 fill:royalblue; /* styling cancel button*/
}
#cancel:hover{
  fill: red;
}
/* creating table with design*/
#customers {
  border-collapse: collapse;
}

#customers td, #customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

#customers tr:nth-child(even){background-color: #f2f2f2;}

#customers tr:hover {background-color: #ddd;}

#customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #e0bc5a;
  color: white;
}

/* Styling the table to manage scrolling */
table {
  display:block;
}

th{
  background: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 2px -1px rgba(0, 0, 0, 0.4);
}
table th,table td{
  width:100px;
}
table  tbody{
display:block;
height: 50px;
max-height:100px;
}

.App{
    display: flex;
    flex-direction: column;
    padding: 1%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}
.portfolioAdd{
  font-family: 'courier';
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  
}
#inputPorfolio{
  font-family:  'courier';
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 50%;
  padding: 10px 20px;
  
}
.button {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 5px 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    font-family: 'courier';
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    
  }
  .buttonAdd {
    background-color: white;
    color: black;
    border: 2px solid #555555;
  }
  .buttonAdd:hover:enabled {
    cursor: pointer;
    background-color: rgba(188, 236, 125, 0.966);
    color: black;
    border: 2px solid #555555;
  }
 
  .content{
    display: flex;
    flex-direction:row;
    justify-content: space-between;
    flex-flow: wrap;
  }
 
  
 
#name{
    text-transform: uppercase;
  }
.mainContainer{
    display: flex;
    z-index: 1;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
   
}

.graphContainer{
    display: flex;
    flex-direction: column;
    font-family: "courier";
    border: 2px solid black;
    padding: 1%;
    margin: 5%;
    width:80%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: #fefefe;
}

.portfolioName{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}
.graphCollection{
